export default {
	name:"fenlei",
	data() {
		return {
			category:[]
		}
	},
	methods:{
		getcategory() {
			uni.request({



				

				url:"http://157.122.54.189:9088/image/v1/vertical/category",

					
				success: (result) => {
					//console.log(result.data.res.category);
					this.category = result.data.res.category;
				}
			})
		},
		tocategory(id) {
			uni.navigateTo({
				url:"./prctureCategory/prctureCategory?id="+id
			})
		}
	},
	mounted(){
		uni.setNavigationBarTitle({
		    title: '分类'
		});
		this.getcategory();
	},
	
}
